import React from 'react'

let listeners = {}

let globalState = null

let setGlobalState = state => Object.values(listeners)
  .forEach(listener => listener(state))

export let useGlobalState = v => {
  let [state, setState] = React.useState(globalState || v)
  globalState = state
  let id = React.useRef(Date.now())

  React.useEffect(() => {
    listeners[id] = setState
    return () => delete listeners[id]
  }, [])

  return [state, setGlobalState]
}
