import React from 'react'

import './style.css'

export let View = ({children, ...props}) =>
  <div {...props}> {children}</div>

export let List = ({children, ...props}) => 
  <ul {...props}>
    {children.map((x, i) => <li key={i}>{x}</li>)}
  </ul>

export let Table = ({children, cols, ...props}) => {
  return <table {...props}>
    <thead>
      <tr>
        {cols.map((col, i) => <th key={i}>{col}</th>)}
      </tr>
    </thead>
    
    <tbody>
      {children.map(
        (row, i) => <tr key={i}>{row.map(
          (item, i) => <td key={i}>{item}</td>
        )}</tr>
      )}
    </tbody>
  </table>
}

export let Title = ({children, level = 1, ...props}) => {
  const Tag = `h${level > 6 ? 6 : level}`
  
  return (
    <Tag {...props}>{children}</Tag>
  )
}

export let Text = ({children, ...props}) =>
  <p {...props}>{children}</p>

export let Image = ({alt, svg = false, src, ...props}) => {
  return svg
    ? <svg {...props}><use href={src}></use></svg>
    : <img alt={alt || "image"} src={src} {...props} />
}

let Field = ({children, label, id, swap = false}) =>
  <div>
    {label && !swap && <label htmlFor={id}>{label}</label>}
    {children}
    {label && swap && <label htmlFor={id}>{label}</label>}
  </div>

export let TextField = ({type, label, ...props}) => 
  <Field label={label} id={props.id}>
    <input type={['email', 'password'][type] || 'text'} {...props} />
  </Field>
  
export let TextEditor = ({value, label, ...props}) =>
  <Field label={label} id={props.id}>
    <textarea {...props}>{value}</textarea>
  </Field>

export let Toggle = ({type, label, ...props}) =>
  <Field label={label} id={props.id} swap={true}>
    <input type="checkbox" {...props} />
  </Field>

export let Picker = ({list = true, name, label, children, ...props}) =>
  list
    ? <Field label={label} id={props.id}>
      <select name={name} {...props}>
        {Object.entries(children).map(
          ([k, v]) => <option key={k} value={v}>{k}</option>
        )}
      </select>
    </Field>
    : <fieldset {...props}>
      <legend>{label}</legend>
      {Object.entries(children).map(
        ([k, v]) => <Field key={k} label={k} id={k} swap={true}>
          <input type="radio" name={name} id={k} value={v} />
        </Field>
      )}
    </fieldset>

export function Stepper({className = '', value = 1, ...props}) {
  let inputRef = React.useRef(null)

  let addOne = x => x + 1
  let subOne = x => x - 1

  let calc = React.useCallback(
    f => () => {
      let setValue = Object.getOwnPropertyDescriptor(
        window.HTMLInputElement.prototype,
        'value'
      ).set;
      
      let newV = f(+value)
      setValue.call(
        inputRef.current,
        newV < props.min || newV > props.max ? value : newV
      );
      inputRef.current.dispatchEvent(new Event('input', { bubbles: true }));
    },
    [value, props.min, props.max]
  )
  
  return <View className={`stepper ${className}`}>
    <Button onClick={calc(subOne)}>-</Button>
    <input ref={inputRef} type="number" value={value} {...props} />
    <Button onClick={calc(addOne)}>+</Button>
  </View>
}

export let Link = ({children, ...props}) =>
  <a {...props}>{children}</a>

export let Button = ({children, ...props}) =>
  <button type="button" {...props}>
    {children}
  </button>

export let Line = ({className = ''}) => 
  <span className={`line ${className}`}></span>
