import React from 'react'
import {View, List, Picker, Button} from './../base'
import {Link} from './../core/core'
import {Logo} from './../logo'

import './style.css'

let NestedMenu = ({children, title}) => {
  let [opened, toggle] = React.useState(false)

  return <>
    <Button className="Menu-nested" onClick={() => toggle(!opened)}>
      {title}<span>{opened ? '-' : '+'}</span>
    </Button>
    {opened && children}
  </>
}

// TODO: should I use useCallback for onClick handlers?
export let Menu = () => {
  let [opened, open] = React.useState(false)

  return <>
    <Button className="Menu" onClick={() => open(!opened)}>
      <span {...(opened ? {className: 'close-btn'} : {})}></span>
    </Button>
    
    {opened && <View>
      <Logo />
      <nav onClick={(e) => open(e.target.localName !== 'a')}>
        <List>
          <NestedMenu title="Catalog">
            <List>
              <Link to="/catalog/caviar">Caviar</Link>
              <Link to="/catalog/truffles">Truffles</Link>
              <Link to="/catalog/gift-sets">Gift sets</Link>
            </List>
          </NestedMenu>
          <Link to="/brands">Brands</Link>
          <Link to="/catering">Catering</Link>
          <Link to="/wholesale">Wholesale</Link>
          <Link to="/contact-us">Contact</Link>
          <Link to="/faqs">FAQs</Link>
        </List>
      </nav>

      <form>
        <Picker>
          {{
            USD: 'usd',
            AED: 'aed'
          }}
        </Picker>
      </form>
    </View>}
  </>
}
