let OrderItem = ({img, name, desc, price, qty, className, ...props}) => {
  let css = [
    'Order-item',
    className || ''
  ].join(" ")

  return <View className={css} {...props}>
    <Image />
    <View>
      <Title level="4">{name}</Title>
      <Text>{desc}</Text>
    </View>
  </View>
}

let Order = ({order, ...props}) => {
  let css = props.className
    ? `Order ${props.className}`
    : 'Order'
  
  return <View {...props} className={css}>
    <Title level="4">Order ID <span>{order.id}</span></Title>
    <span className="Order-status">
      {order.status === 'Finished' ? order.status : 'Upcoming'}
    </span>
    
    <Text>
      {order.products.length}
      {" "}{order.products.length > 1 ? 'items' : 'item'}
      {" "}for <span>{order.total}</span>
    </Text>
    
    <ProductsListPreview>
      {order.products}
    </ProductsListPreview>
    
    <View className="Order-actions">
      <Link to={`/my-account/order/${order.id}`}
        className="primary-btn"
      >
        View details
      </Link>
      {order.status === 'Finished'
        ? <Link to={`/my-account/order/${order.id}/repeat`}
          className="accent-btn"
        >
          Buy again
        </Link>
        : <Link to={`/my-account/order/${order.id}/track`}
          className="accent-btn"
        >
          Track order
        </Link>}
    </View>

    <View className="Order-actions">
      <Link to={`/my-account/order/${order.id}/invoice`}>Get invoice</Link>
      {order.status === 'Finished'
        ? <Link to={`/my-account/order/${order.id}/refund`}>
          Request refund
        </Link>
        : <Link to={`/my-account/order/${order.id}/change`}>
          Edit or cancel order
        </Link>}
    </View>
  </View>
}

export let Profile = () => {
  let [ordersVisible, setOrdersVisible] = React.useState(false)

  return <View className="Profile">
    <PageTitle>Account</PageTitle>
    <Title level="2">My account</Title>
    <Line />

      {/* <Link to="/my-account/delete">Delete my account</Link> */}
    <Link to="/logout" className="Profile-logout">Log out</Link>

    <View className="Profile-details">
      <View>
        <Title level="3">Account information</Title>
        <InlineEdit
          attrs={[
            {label: 'First name', name: 'firstName', value: user.firstName},
            {label: 'Last name', name: 'lastName', value: user.lastName},
            {label: 'Email', name: 'email', value: user.email},
            {label: 'Phone number', name: 'phone', value: user.phone}
          ]}
        >
          <List>
            {Object.entries(user).reduce(
              (acc, [k, v]) => {
                if (k === 'shipmentInfo' || k === 'orders') return acc

                if (k === 'firstName') return [v, ...acc]
                if (k === 'lastName') return [`${acc[0]} ${v}`,
                ...acc.slice(1)]

                return [...acc, v]
              },
              []
            )}
          </List>
        </InlineEdit>

        <InlineEdit className="Profile-change-password"
          trigger={<a href="#change-password">Change password</a>}
          attrs={[
            {label: 'Current password', name: 'currentPassword', value: ''},
            {label: 'New password', name: 'newPassword', value: ''},
          ]}
        ></InlineEdit>
      </View>

      <View id="Profile-info-address">
        <Title level="3">Address book</Title>
        {user.shipmentInfo.map(
          (x, i) => <InlineEdit key={i}
            attrs={[
              {label: 'Address', name: 'address', value: x.address},
              {label: 'City', name: 'city', value: x.city},
              {label: 'Post code', name: 'postcode', value: x.postcode},
            ]}
          >
            <Text>
              {Object.values(x).join(" ")}
            </Text>
          </InlineEdit>
        )}
      </View>
    </View>
    
    <Line />

    <View className="Profile-orders">
      <Title level="2">Order history</Title>
      <Text>{user.orders.all} orders</Text>

      {ordersVisible
        ? <View className="Profile-orders-details">
          {orders.map(o => <Order key={o.id} order={o} />)}
        </View>
        : <>
          <Text>You have {user.orders.new} upcoming order.</Text>
          <Button className="primary-btn"
            onClick={() => setOrdersVisible(true)}
          >
            View my orders
          </Button>
        </>}
    </View>
  </View>
}
