import React from 'react'
import {Image, Title, Text, List} from './../base'

import './style.css'

export let Product = ({img, name, desc}) => <>
  <Image src={img} alt={`${name} image`} />
  <Title level="4">{name}</Title>
  <Text>{desc}</Text>
</>

export let ProductsList = ({children, className}) => {
  let css = [
    'Products-list',
    className || ''
  ].join(" ")
    
  return <List className={css}>
    {children.map(p => React.isValidElement(p) ? p : <Product key={p.id}
      name={p.name}
      img={p.img}
      desc={`(${p.weight}) x ${p.qty}`}
    />)}
  </List>
}

export let ProductsListPreview = ({children, ...props}) => {
  return <ProductsList {...props}>
    {[
      ...children.filter((x, i) => i < 3),
      <Text key="rest-count">{children.length > 3 ? `+${children.length - 3}` : ''}</Text>
    ]}
  </ProductsList>
}
