import React from 'react'

export const useForm = (values, cb) => {
  let [isRefresh, toggleRefresh] = React.useState(false)
  const [inputs, setInputs] = React.useState(values)

  React.useEffect(() => {
    setInputs(values)
    !isRefresh && toggleRefresh(true)
  }, [isRefresh])
  
  const submit = e => {
    if (e) e.preventDefault()
    
    cb(
      [...e.target.elements].reduce((acc, el) => {
        if (acc[el.name] !== undefined) acc[el.name] = el.value
        return acc;
      }, inputs)
    )
    setInputs(values)
  }

  const change = e => {
    e.persist()
    setInputs(inputs => ({...inputs, [e.target.name]: e.target.value}))
  }

  return {submit, change, inputs}
}
