import React from 'react'
import {Link as BaseLink, View, Button, TextField, Image} from './../base'
import {Link as RouterLink} from 'react-router-dom'
import {useForm} from './../../libs/use-form'
import iconPen from './../../icons/pen.svg'

import './style.css'

export let Link = ({children, to, ...props}) =>
  to == null
    ? <BaseLink {...props}>{children}</BaseLink>
    : <RouterLink to={to} {...props}>{children}</RouterLink>


export let PageTitle = ({children}) => <span className="PageTitle">
    {children}
  </span>


export let InlineEdit = ({
  trigger,
  attrs,
  onSubmit,
  children,
  className, 
  ...props
}) => {
  let [isEditMode, setEditMode] = React.useState(false)

  let form = useForm(
    attrs.reduce(
      (fields, attr) => ({...fields, [attr.name]: attr.value}),
      {}
    ),
    onSubmit
  )

  let css = ['inline-edit', className || ''].join(" ")

  return <View className={css} {...props}>
    {!isEditMode && <Button
      onClick={() => setEditMode(true)}
    >
      {trigger || <Image svg={true} src={`${iconPen}#pen`} />}
    </Button>}
    {isEditMode
      ? <form onSubmit={form.submit}>
        {attrs.map(
          attr => <TextField key={attr.name}
            label={attr.label}
            name={attr.name}
            id={attr.name}
            value={form.inputs[attr.name]}
            onChange={form.change}
          />
        )}

        <View onClick={() => setEditMode(false)}>
          <Button className="primary-btn">
            Cancel
          </Button>
          <Button type="submit" className="accent-btn">Save changes</Button>
        </View>
      </form>
      : children}
  </View>
}

