import React from 'react'
import {Image} from './../base'

import fullLogo from './../../icons/full_logo.svg'
import shortLogo from './../../icons/short_logo.svg'

export const Logo = ({isSmall = false, ...props}) => {
  return <Image
    {...props}
    svg={true}
    src={isSmall ? `${shortLogo}#short_logo` : `${fullLogo}#full_logo`}
    alt="Logo of Anikley gourmet"
  />
}
