import React from 'react'
import {
  View, Image, Title, List, Text, Line, Button, TextField, Toggle
} from './../base'
import {Link} from './../core/core'
import iconProfile from './../../icons/user.svg'
import iconBag from './../../icons/shopping-bag.svg'
import {footerLinks} from './footer-links'
import {Menu} from './../menu'
import {Logo} from './../logo'

import './style.css'

let Header = () => {
  return <View className="Header">
    <Menu />
    
    <Link to="/">
      <Logo isSmall={true} />
    </Link>
    
    <View>
      <Link to="/profile">
        <Image svg={true} src={`${iconProfile}#user`} alt="profile icon" />
      </Link>
      <Link to="/cart">
        <Image svg={true} src={`${iconBag}#bag`} alt="bag icon" />
      </Link>
    </View>
  </View>
}

let Footer = () => {
  return <View className="Footer">
    <Logo className="Footer-logo" />

    <View className="Footer-links">
      {Object.entries(footerLinks).map(
        ([title, links]) => {
          return <View key={title}>
            <Title level="4" className="Footer-title">{title}</Title>
            <List>
              {Object.entries(links).map(
                ([label, url]) => <Link key={label} to={url}>{label}</Link>
              )}
            </List>
          </View>
        }
      )}
    </View>
      
    <Text className="Footer-copyright">
      All rights reserved - 2020 {"\u00A9"} Anikley Gourmet
    </Text>
  </View>
}

