let CookieNotice = () => {
  let [isActive, toggle] = React.useState(false)

  return isActive && <View className="CookieNotice">
    <Button
      className="close-btn"
      onClick={() => toggle(false)}
    ></Button>
    <Text>This website uses cookies to give you the best shopping experience. By continuing to browse the website you agree to the use of such cookies. Learn more about cookies and how we use them <Link to="/privacy-policy">here</Link>.</Text>
  </View>
}

